import React from 'react';
import { connect } from 'react-redux';
import {  } from '../actions';

export function Stepper (props) {

  const Dot = props => <div {...styles.dot(props.active)}></div>;
  const Dots = (pageCount, pageIndex, isActive) => Array(pageCount).fill(null).map( (_, i) => <Dot key={i} active={pageIndex === i && isActive}/> );
  const ReviewSection = (isActive, pageIndex, pageCount) => {
    if (!pageCount) return null;
    return <section {...styles.section(isActive)}>
        {pageCount > 1 ? <p {...styles.label(isActive)}>revi{Dots(pageCount, pageIndex, isActive)}</p> : <p {...styles.label(isActive)}>review</p> }
      </section>;
  }
  const DoSection = (isActive, pageIndex, pageCount) => {
    if (!pageCount) return null;
    return <section {...styles.section(isActive)}>
        {<p {...styles.label(isActive)}>d{Dots(pageCount, pageIndex, isActive)}</p>}
      </section>;
  }

  const Sections = (pageIndexes, sectionIndex) => {
    return (
      <React.Fragment>
        <section {...styles.section(sectionIndex === 0)}><p {...styles.label(sectionIndex === 0)}>dr<Dot active={sectionIndex === 0} />p</p></section>
        {ReviewSection(sectionIndex === 1, pageIndexes[1], props.reviewables.length)}
        {DoSection(sectionIndex === 2, pageIndexes[2], props.areas.length)}
      </React.Fragment>
    );
  }

  return (
    <div {...styles.container}>
      {Sections(props.pageIndexes, props.sectionIndex)}
    </div>
  );
};

const styles = {
  dot: isActive => ({
    style: {
      display: 'inline-block',
      width: '8px',
      height: '8px',
      margin: '8px 1px 0',
      borderRadius: '50%',
      border: '1px solid #eca690',
      transition: 'all 250ms ease-out',
      ...( isActive && {
        border: 'none',
        backgroundColor: '#ffe1e1de'
      })
    }
  }),
  section: isActive => ({
    style: {
      display: 'flex',
      flexDirection: 'row',
      ...( isActive && {
      })
    }
  }),
  label: isActive => ({
    style: {
      margin: 0,
      fontSize: '0.87rem',
      color: '#eca690',
      transition: 'all 250ms ease-in',
      ...( isActive && {
        color: '#ffe1e1de'
      })
    }
  }),
  container: {
    style: {
      width: '100%',
      position: 'absolute',
      bottom: '5px',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      userSelect: 'none'
    }
  }
}

const mapStateToProps = state => ({
  pageIndexes: state.pageIndexes,
  sectionIndex: state.sectionIndex,
  doables: state.doables,
  reviewables:  state.doables.filter( doable => doable.area === ''),
  areas: [...new Set(state.doables.map( doable => doable.area ).filter( area => area !== '' ))]
});

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(Stepper);
